body {
  font-family: 'Roboto', sans-serif;
}

h1 { margin: .67em 0 }
h2 { margin: .75em 0 }
h3 { margin: .83em 0 }
h5 { margin: 1.5em 0 }
h6 { margin: 1.67em 0 }

.navbar-brand img {
  margin: 0 14px 0 28px;
}

.nav-item {
  margin-left: 1.3rem;
}

.nav-link {
  color: #393939 !important;
  font-weight: 400;
}

.active {
  border-bottom: 2px #dadada solid;
  margin: 1px .5rem 0;
  padding-right: 0 !important;
  padding-left: 0 !important;
}

footer a {
  color: #666;
  text-decoration: none;
  font-size: .8rem;
}

footer a:hover, footer a:active {
  color: #333;
  text-decoration: underline;
}

footer img {
  margin-right: 10px;
  height: 50px;
}

.icon-wrapper {
  margin-left: 5px;
  position: relative;
  top: -1px;
}

a {
  color: #00807f;
  text-decoration: none;
}
a:hover, a:active {
  color: #00807f;
  text-decoration: underline;
}

/* Undo the reboot.scss from bootstrap so we aren't assuming things! */
#map-container *,
#map-container *::before,
#map-container *::after {
  box-sizing: content-box;
}

.tabs-body {
  margin-top: 20px;
  margin-bottom: 20px;
}

.tabs-body .tabs {
  -webkit-overflow-scrolling: touch;
  display: flex;
  margin: 0 10px 20px;
  overflow-x: auto;
  scroll-behavior: smooth;
  scrollbar-width: none;
  border-bottom: 1px solid #ebe8e3;
}

.tabs-body .tabs::-webkit-scrollbar {
  display: none;
}

.tabs-body .nav-item {
  margin-left: 0;
}

.tabs-body a:hover,
.tabs-body .nav-link:hover {
  text-decoration: none;
}

.tabs-body a {
  font-weight: 500;
}

.tabs-body .nav-item:first-child {
  margin-left: -10px;
}

.tabs-body .nav-link {
  font-weight: 500;
  margin: 10px 10px 0;
  padding: 10px 10px 20px;
  white-space: nowrap;
  border: 0;
}
.tabs-body .nav-link:hover {
  border: 0;
}

.tabs-body .nav-link:first-child {
  margin-left: 1px;
}

.tabs-body .nav-link:last-child {
  margin-right: 1px;
}

.tabs-body .nav-link > * {
  color: #393939;
}

.tabs-body .nav-tabs .nav-link.active {
  color: #00807f !important;
  position: relative;
  padding-left: 10px !important;
  padding-right: 10px !important;
}
.tabs-body .nav-tabs .nav-link.active::before {
  content: '';
  position: absolute;
  bottom: 0;
  width: calc(100% - 10px * 2);
  left: 10px;
  border-bottom: 2px solid #00807f !important;
}
.tabs-body .nav-tabs .nav-link.active:focus {
  outline: 1px solid #00807f;
}
.tabs-body .nav-tabs .nav-link.active:focus::before {
  width: 100%;
  left: 0;
}

